.contact {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;

  img {
    width: 40%;
    max-width: 1000px;
    height: 50%;
    object-fit: cover;
  }

  .contactConatiner {
    max-width: 800px;
    margin: 0 2rem;
    text-align: center;

    h1 {
      font-size: 4rem;
      margin-bottom: 2rem;
      text-transform: uppercase;
      background-color: #1c1c1c;
      color: #fff;
      border-radius: 5px;
    }

    p {
      font-size: 1.5rem;
      margin-bottom: 1rem;

      span {
        width: 40px;
        text-align: left;
        text-transform: capitalize;
      }

      svg {
        margin-right: 0.5rem;
        vertical-align: middle;
      }
    }
  }
}
@media only screen and (max-width: 768px) {
  .contact {
    flex-direction: column;
    margin: 0;
    margin-bottom: 150px;

    img {
        width: 100%;
        height: 80%;
      }
    .contactConatiner {
        margin: 50px;

      h1 {
        font-size: 3rem;
      }

      p {
        font-size: 1.2rem;
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .contact {
    margin-bottom: 50px;
    .contactConatiner {
      margin: 10px;

      h1 {
        font-size: 2.5rem;
        margin-bottom: 1.5rem;
      }

      p {
        font-size: 1rem;
        margin-bottom: 0.5rem;

        span {
        //   display: block;
          margin-bottom: 0.5rem;
          width: auto;
        }
      }
    }
  }
}
