.searchContainer {
  width: 100%;
  form {
    display: flex;
    align-items: center;
    border: 0.5px ridge gray;
    margin-left: 14px;
    margin-bottom: 7px;
    width: 90%;
    border-radius: 2px;
    height: 32px;

    .searchicon {
      background-color: #fff;
      color: #333;
      height: 108%;
      padding: 0 5px;
    }
    input {
      border: none;
      padding: 10px;
      width: 100%;
      outline: none;
      background-color: #fff;
    }
  }

  @media (max-width: 950px) {
    form {
      .searchicon {
        height: 110%;
        margin-top: 1px;
      }
    }
  }
  @media (max-width: 950px) {
    form {
      margin-top: 4px;
      background-color: #fff;
      z-index: 99999;
    }
  }
}
