.users {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  
    @media (max-width: 850px) {
      flex-direction: column;
      align-items: center;
    }
  
    .usersLinks {
      width: 20%;
      margin-right: 2rem;
  
      @media (max-width: 850px) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 2rem;
      }

    }
  
    .usersContent {
      width: 75%;
  
      @media (max-width: 850px) {
        width: 90%;
      }
    }
  
    .card {
      background-color: #fff;
      padding: 2rem;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      margin-top: 50px;
    }
  }
  
  .adminMenu {
    background-color: #fff;
    padding: 1rem;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
  
    .menuItem {
      margin-bottom: 1rem;
  
      a {
        color: #333;
        text-decoration: none;
  
        &:hover {
          color: #555;
        }
      }
    }
  }
  
