.about {
  background-color: #fff;
  padding: 80px 20px;

  h2 {
    font-size: 48px;
    margin: 40px;
    margin-top: 0;
    text-align: center;
    text-transform: uppercase;
  }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      width: 40%;
      margin-right: 50px;
    }

    .textContainer {
      width: 55%;

      p {
        margin-bottom: 30px;
        line-height: 1.5;
      }
    }
  }

  // Media query for max-width: 768px
  @media screen and (max-width: 768px) {
    padding: 60px 0;

    h2 {
      font-size: 36px;
      margin-bottom: 30px;
    }

    .container {
      flex-direction: column;
      align-items: center;

      img {
        width: 70%;
        margin-bottom: 30px;
        margin-right: 0;
      }

      .textContainer {
        width: 100%;
        text-align: center;
      }
    }
  }

  // Media query for max-width: 480px
  @media screen and (max-width: 480px) {
    padding: 40px 0;

    h2 {
      font-size: 24px;
      margin-bottom: 20px;
    }

    .container {
      img {
        width: 90%;
      }
    }
  }
}
