@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");

.user-menu {
  h1{
    text-align: center;
    margin-right: 80px;
  }
  ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    margin: 50px;

    li {
      margin: 10px;
      font-family: "Poppins", sans-serif;
      font-weight: 700;
      &:last-child {
        margin-right: 0;
      }

      a {
        color: #241e1e;
        display: block;
        padding: 1rem;
        text-decoration: none;

        &.active {
          background-color: #0400ff;
          border-radius: 20px;
          color: #fff;
        }
      }
    }
  }
}
