.SearchContainer {
  margin: 0 auto;
  max-width: 1285px;
  padding: 20px;
  background-color: #fff;
}

.SearchContainerText {
  margin-bottom: 10px;
}

h6 {
  font-size: 16px;
  font-weight: bold;
}

.ProductSearch {
  display: flex;
  flex-wrap: wrap;
}

.ProductSearchCard {
  border: 1px solid #ccc;
  margin-bottom: 10px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 50px;
}

.ProductSearchImg {
  width: 40%;
  height: auto;
  max-height: 300px;
  max-width: 300px;
  object-fit: cover;
}

.card-body {
  padding: 10px;
  width: 30%;
}

.card-body-content {
  display: flex;
  flex-direction: column;
}

.card-title {
  font-size: 18px;
  font-weight: bold;
}

.card-text {
  font-size: 14px;
  line-height: 1.5;
  margin-top: -15px;
}

.priceContainer {
  display: flex;
  align-items: center;
  margin-top: -15px;
}

.card-price {
  font-size: 18px;
  font-weight: bold;

  span {
    color: red;
  }
}

.card-oldPrice {
  text-decoration: line-through;
  margin: 0 15px;
}

.card-discount {
  background-color: #310d0d;
  color: #fff;
  padding: 2px 5px;
  font-size: 16px;
  font-weight: bold;
}

.CategoryProduct-Rating {
  margin-top: -5px;
  font-weight: bold;
  span {
    background-color: #310d0d;
    color: #fff;
    padding: 2px 5px;
  }
}

.CategoryProduct-Delivery {
  font-size: 14px;
  color: #aaa;
}
.btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btnMoreDetails {
  background-color: #aaa;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.btnAddToCart {
  background-color: #292929;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

@media (max-width: 950px) {
  .ProductSearchCard {
    flex-direction: column;
    align-items: center;
  }
  .ProductSearchImg {
    width: 40%;
  }

  .card-body {
    width: 60%;
  }
}

@media (max-width: 768px) {
  .ProductSearchCard {
    width: 100%;
  }

  .priceContainer {
    flex-direction: column;
  }

  .CategoryProduct-Delivery {
    text-align: center;
  }
}

@media (max-width: 450px) {
  .btns {
    flex-direction: column;
  }
  .btnAddToCart {
    margin-top: 10px;
  }
}
