.CategoriesAll {
  .categoriesContainer {
    .categoriesRowContainer {
      h1 {
        text-align: center;
        text-transform: uppercase;
      }

      .colCategories {
        .card {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0 auto;

          a {
            text-decoration: none;
            border: 1px ridge gray;
            text-align: center;
            width: 200px;
            padding: 10px 30px;
            margin: 10px;
            border-radius: 10px;

            &:hover {
              background-color: rgb(75, 2, 30);
              color: #fff;
            }

            @media screen and (max-width: 300px) {
              width: 90px;
            }
          }
        }
      }
    }

    p {
      width: 80%;
      margin: 40px auto;
    }
  }
}
