.FeaturedProducts {
  margin-top: 50px;

  .FeaturedProductsText {
    text-align: center;
    width: 70%;
    margin: 0 auto;
  }

  .FeaturedProductsimages {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: 20px;
    box-sizing: border-box;
    .image {
      position: relative;
      width: 18%;
      height: 300px;
      object-fit: cover;
      overflow: hidden;
      border-radius: 8px;
      margin: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;

      img {
        width: 100%;
        max-width: 200px;
        margin: 0 auto;
        height: auto;
        object-fit: cover;
        transition: transform 0.3s ease;
      }

      span {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 10px;
        font-weight: bold;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.7);
        transition: opacity 0.3s ease;
        opacity: 0;
        z-index: 1;
      }

      &:hover {
        img {
          transform: scale(1.1);
        }

        span {
          opacity: 1;
        }
      }
    }
  }

  @media (max-width: 950px) {
    .FeaturedProductsimages {
      .image {
        width: 20%;
        margin: 20px;
      }
    }
  }

  @media (max-width: 768px) {
    .FeaturedProductsimages {
      flex-direction: column;
      align-items: center;
      .image {
        width: 45%;
      }
    }
  }

  @media (max-width: 450px) {
    .FeaturedProductsimages {
     
      .image {
        width: 85%;

        img {
          transform-origin: center;
        }

        span {
          font-size: 14px;
          padding: 5px;
        }
      }
    }
  }
}
