.dashboard {
    display: flex;
    justify-content: space-around;

    .col-md-9 {
      .border.shadow {
        border: 1px solid #ccc;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
        padding: 20px;
        margin-bottom: 20px;
      }
  
      table.table {
        width: 100%;
        border-collapse: collapse;
  
        th,
        td {
          padding: 10px;
          border-bottom: 1px solid #ccc;
        }
  
        th {
          text-align: left;
          font-weight: bold;
        }
  
        tbody {
          tr {
            &:nth-child(even) {
              background-color: #f5f5f5;
            }
  
            td {
              .ant-select {
                width: 100%;
  
                &.ant-select-bordered {
                  border: none;
                }
  
                .ant-select-selector {
                  border-radius: 4px;
                }
              }
            }
          }
        }
      }
  
      .container {
        .row.mb-2.p-3.card.flex-row {
          .col-md-4 {
            img.card-img-top {
              width: 50%;
              height: 50%;
              object-fit: cover;
            }
          }
  
          .col-md-8 {
            p {
              margin-bottom: 10px;
  
              &:last-child {
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }
  }
  