.createProducts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: 850px) {
    flex-direction: column;
    align-items: center;
  }

  .createProductsLinks {
    flex: 1;
    margin-right: 2rem;

    @media (max-width: 850px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }

  .createProductsContent {
    margin-top: 100px;
    margin-bottom: 100px;
    flex: 5;

    .card {
      background-color: #fff;
      padding: 2rem;
      box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
        rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
      border-radius: 5px;
      margin-bottom: 100px;

      h2 {
        text-align: center;
      }

      .productContent {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 2rem 0;

        // styles for desktop
        @media (min-width: 851px) {
          .productDiv {
            width: 50%;
          }

          .productDiv:not(:last-child) {
            margin-bottom: 1rem;
          }
        }

        // styles for tablet
        @media (max-width: 850px) {
          .productDiv {
            width: 75%;
          }

          .productDiv:not(:last-child) {
            margin-bottom: 1rem;
          }
        }

        // styles for mobile
        @media (max-width: 450px) {
          .productDiv {
            width: 100%;
          }

          .productDiv:not(:last-child) {
            margin-bottom: 1rem;
          }
        }
        .select {
          width: 50%;
          margin-bottom: 1rem;
          border: 1px ridge gray;
        }
        .productDiv {
          label {
            border: 1px ridge gray;
            padding: 5px;
          }
          input,
          textarea {
            width: 100%;
            padding: 10px;
          }

          .select {
            width: 50%;
            margin-bottom: 1rem;
            border: 1px ridge gray;
          }

          .createBtn {
            background-color: #1890ff;
            color: #fff;
            border: none;
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            cursor: pointer;
            transition: background-color 0.2s ease-in-out;

            &:hover {
              background-color: #40a9ff;
            }
          }
        }
      }
    }
    @media (max-width: 850px) {
      width: 90%;
    }
  }
}
