.userDashboard {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  
    @media (max-width: 850px) {
      flex-direction: column;
      align-items: center;
    }
  
    .userDashboardLinks {
      width: 20%;
      margin-right: 2rem;
  
      @media (max-width: 850px) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 2rem;
      }

    }
  
    .userDashboardContent {
      width: 75%;
  
      @media (max-width: 850px) {
        width: 90%;
      }
    }
  
    .card {
      background-color: #fff;
      padding: 2rem;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      margin-top: 50px;

      h4{
        display: flex;
        align-items: center;
        font-size: 18px;

        @media screen and ( max-width:450px){
          flex-direction: column;
          text-align: center;
         }
         
        p{
          margin-left: 10px;
          font-size: 16px;
          font-weight:500;
        }
      }


    }
  }
  
