.register {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 90px;

  h1 {
    margin-bottom: 20px;
    font-size: 2rem;
    font-family: Arial, Helvetica, sans-serif;
  }

  form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
      rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
      rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    padding: 45px;
    border-radius: 20px;
    margin: 0 10px;

    .form-group {
      flex-basis: 48%;
      margin-bottom: 20px;

      label {
        display: block;
        margin-bottom: 5px;
        font-size: 1.2rem;
      }

      input,
      textarea {
        padding: 0.8rem;
        border: none;
        border-radius: 0.5rem;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        background-color: rgb(201, 220, 221);
        width: 90%;

        &:focus {
          outline: none;
        }
      }

      textarea {
        height: 100px;
        resize: vertical;
      }
    }

    .passwordDiv {
      .passwordDivChild {
        position: relative;
        .passwordToggleBtn {
          position: absolute;
          top: 73%;
          right: 10px;
          transform: translateY(-50%);
          border: none;
          background-color: transparent;
          cursor: pointer;
          color: #888;
          font-size: 14px;

          &:focus {
            outline: none;
          }
        }
      }

      .passwordError {
        margin-top: 0px;
        font-size: 10px;
        color: red;
      }
    }

    button[type="submit"] {
      margin-top: 20px;
      height: 50px;
      width: 100%;
      text-transform: uppercase;
      padding: 10px 20px;
      border-radius: 5px;
      border: none;
      background-color: #007bff;
      color: #fff;
      font-size: 1.2rem;
      cursor: pointer;
      transition: all 0.3s ease;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
        rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
        rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
      &:hover {
        background-color: #0062cc;
      }
    }

    .formtext {
      display: flex;
      align-items: center;
      margin: 22px 10px;
      font-size: 20px;

      p {
        margin-right: 10px;
      }
      a {
        text-decoration: none;
      }
    }
  }

  @media screen and (max-width: 768px) {
    form {
      .form-group {
        flex-basis: 100%;
      }
    }
  }

  @media screen and (max-width: 480px) {
    form {
      .form-group {
        flex-basis: 100%;
      }

      button[type="submit"] {
        width: 100%;
      }
    }
  }
}
