$color: #007bff;
$thickness: 5px;
.bossSpinner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;

  h1 {
    margin-bottom: 40px;
  }
  .Spinner {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: #ccc solid $thickness;
    border-top-color: $color;
    animation: spin 1s ease-in-out infinite;
    @keyframes spin {
      to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }
}
