.ProductsDashboard {
  margin: 0 auto;
  max-width: 1200px;
  padding: 20px;
  border: 1px solid #ccc;

  @media (max-width: 768px) {
    width: 100%;
    padding: 0 10px;
  }
}

.productsDashboardLinks {
  margin-bottom: 20px;
}

.productsDashboardContent {
  padding: 20px;
}

.card {
  margin-bottom: 20px;
}

.text-center {
  text-align: center;
}

.dashboardContent {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 20px;

  @media (max-width: 768px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.product-link {
  width: 100%;
  border: 1px solid #ccc;
  text-decoration: none;
}

.cardData {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.card-img-top {
  width: 50%;
  height: 50%;
  max-height: 250px;
  max-width: 250px;
  object-fit: cover;
  border: 1px solid #ccc;

  @media (max-width: 768px) {
    width: 100%;
    height: 100%;
  }
}

.card-body {
  padding: 10px;
}

.card-title {
  font-size: 18px;
  font-weight: bold;
}

.card-text {
  font-size: 14px;

  span{
    margin: 0 10px;
  }
}

.size {
  font-size: 14px;
}

.rating {
  font-size: 14px;
  color: #666;
}

.description {
  font-size: 14px;
  color: #666;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}