.footer {
  background-color: #2a2b2e;
  padding: 50px;
  color: #fff;

  .footerContainer {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    font-family: Arial, Helvetica, sans-serif;

    hr {
      position: relative;
      border: none;
      height: 2px;
      background-color: #ffffff;
      margin: 0.2em 0;

      &::before {
        content: "";
        position: absolute;
        top: -0.5rem;
        left: 0;
        width: 50%;
        height: 1rem;
        background-color: #f93;
      }

      &::after {
        content: "";
        position: absolute;
        top: -0.5rem;
        right: 0;
        width: 50%;
        height: 1rem;
        background-color: #128807;
      }
    }

    .footerTextSection {
      flex-basis: 40%;
      margin-bottom: 30px;

      h4 {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 20px;
      }

      p {
        font-size: 16px;
        line-height: 1.6;
        margin-bottom: 0;
      }
    }

    .footerPagesSection {
      flex-basis: 20%;

      h4 {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 20px;
      }

      ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          margin-bottom: 10px;

          a {
            font-size: 16px;
            text-decoration: none;
            transition: color 0.2s ease-in-out;
            color: #fff;
            &:hover {
              color: #007bff;
            }
          }
        }
      }
    }

    .socialMedia {
      flex-basis: 20%;

      h4 {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 20px;
        color: yellow;
      }

      ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          margin-bottom: 10px;

          a {
            display: inline-flex;
            align-items: center;
            font-size: 16px;
            text-decoration: none;
            transition: color 0.2s ease-in-out;
            color: #fff;
            &:hover {
              color: #007bff;
            }

            svg {
              margin-right: 10px;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
  .copyrightSection {
    text-align: center;
    margin-top: 50px;

    p {
      font-size: 14px;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 768px) {
  .footerContainer {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .footerTextSection {
    flex-basis: 100%;
    margin-bottom: 20px;
    text-align: left;
  }
  .footerPagesSection {
    flex-basis: 50%;
    margin: 0;
    margin-bottom: 20px;
  }
  .socialMedia {
    flex-basis: 50%;
  }
}

@media (max-width: 480px) {
  .footerContainer {
    align-items: center;
  }
  .footerTextSection {
    flex-basis: 100%;
    margin-bottom: 20px;
    text-align: center;
  }
  .footerPagesSection,
  .socialMedia {
    flex-basis: 100%;
    text-align: center;
  }
}
