nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3f50e6;
  color: #ffffff;
  padding: 20px;
  top: 0;
  position: sticky;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  z-index: 999;

  .logo {
    display: flex;
    align-items: center;
    width: 50%;
    height: 50px;
    img {
      height: 40px;
    }

    @media (max-width: 950px) {
      flex-direction: column;
      // align-items: flex-start;
      margin: 10px 0;
      width: 100%;
      height: 60px;
    }
  }

  .links-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 20px;

    ul {
      display: flex;
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin-right: 15px;
        position: relative;

        .dropdown-category {
          cursor: pointer;
          text-transform: uppercase;
          display: flex;
          align-items: center;

          .dropIcon {
            margin-left: 3px;
            font-size: 20px;
          }

          &:hover {
            .dropdown-menu-category {
              display: block;
            }
          }
        }
        .dropdown-menu-category {
          display: none;
          position: absolute;
          top: 100%;
          left: 0;
          min-width: 100%;
          z-index: 9999;
          background: #3f6bfa;
          padding: 10px;
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
          &.open {
            display: block;
          }
          li {
            a {
              display: block;
              padding: 5px 10px;
              color: #ffffff;
              text-decoration: none;
              &:hover {
                background-color: rgba(37, 41, 53, 0.295);
              }
            }
          }
        }

        .dropdown {
          cursor: pointer;
          text-transform: uppercase;
          display: flex;
          align-items: center;

          .dropIcon {
            margin-left: 3px;
            font-size: 20px;
          }

          &:hover {
            .dropdown-menu {
              display: block;
            }
          }
        }
        .dropdown-menu {
          display: none;
          position: absolute;
          top: 100%;
          left: 0;
          min-width: 100%;
          z-index: 9999;
          background: #3f6bfa;
          padding: 10px;
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
          &.open {
            display: block;
          }
          li {
            a {
              display: block;
              padding: 5px 10px;
              color: #ffffff;
              text-decoration: none;
              &:hover {
                background-color: rgba(37, 41, 53, 0.295);
              }
            }
          }
        }
        a {
          color: #ffffff;
          text-decoration: none;
          text-transform: uppercase;
        }
      }
    }

    .cartIconContainer {
      position: relative;
      display: flex;
      align-items: center;
      margin-left: 10px;
      color: #ffffff;
      text-decoration: none;

      .cartIcon {
        font-size: 1.5rem;
      }

      .iconno {
        position: absolute;
        top: -10px;
        right: -10px;
        background-color: red;
        color: #fff;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.8rem;
      }
    }
  }
}

@media (max-width: 1000px) {
  nav {
    flex-direction: column;
    align-items: center;
    padding-top: 5px;
    padding-bottom: 10px;
    .links-container {
      margin-top: 20px;
      margin-bottom: 10px;
    }
  }
}

@media (max-width: 950px) {
  nav {
    .logo {
      img {
        height: 30px;
      }
    }
  }
}

@media (max-width: 600px) {
  nav {
    .links-container {
      font-size: 10px;
      margin-right: 0;
      ul{
        li{
          margin-right: 9px;
        }
      }
      .cartIconContainer {
        .cartIcon {
          font-size: 1.2rem;
          margin: 0;
        }
      }
    }
  }
}