.privacy-policy {
  padding: 50px 0;
  h2 {
    font-size: 40px;
    margin-bottom: 30px;
    text-align: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
  }

  h3 {
    font-size: 26px;
    margin: 30px 0 20px;
  }

  p {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 25px;
    text-align: justify;
  }

  // Media Query for max-width: 768px
  @media only screen and (max-width: 768px) {
    h2 {
      font-size: 32px;
      margin-bottom: 20px;
    }

    h3 {
      font-size: 24px;
      margin: 25px 0 15px;
    }

    p {
      font-size: 16px;
      margin-bottom: 20px;
    }
  }

  // Media Query for max-width: 480px
  @media only screen and (max-width: 480px) {
    h2 {
      font-size: 28px;
      margin-bottom: 15px;
    }

    h3 {
      font-size: 20px;
      margin: 20px 0 10px;
    }

    p {
      font-size: 14px;
      margin-bottom: 15px;
    }
  }

  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
}
