.form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  
    input {
      width: 100%;
      max-width: 300px;
      padding: 10px;
      font-size: 16px;
      border: 1px solid #ccc;
      border-radius: 5px;
      outline: none;
      transition: all 0.3s ease;
  
      &:focus {
        border-color: #007bff;
      }
    }
  
    button {
      display: block;
      margin: 0 auto;
      padding: 10px 20px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      cursor: pointer;
      transition: all 0.3s ease;
  
      &:hover {
        background-color: #0069d9;
      }
  
      &:active {
        transform: translateY(1px);
      }
    }
  
    @media screen and (max-width: 850px) {
      input {
        max-width: 250px;
      }
    }
  
    @media screen and (max-width: 450px) {
      input {
        max-width: 150px;
        font-size: 14px;
      }
  
      button {
        padding: 8px 16px;
        font-size: 14px;
      }
    }
  }
  