.pagenotfound {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
  
    h1 {
      font-size: 8rem;
      font-weight: bold;
      margin-bottom: 0;
    }
  
    h2 {
      font-size: 2rem;
      margin-top: 0;
      text-align: center;
    }
  
    a {
      display: inline-block;
      margin-top: 2rem;
      padding: 1rem 2rem;
      font-size: 1.5rem;
      font-weight: bold;
      text-decoration: none;
      color: white;
      background-color: #007bff;
      border-radius: 5px;
      transition: background-color 0.3s ease-in-out;
  
      &:hover {
        background-color: #0062cc;
      }
    }
  }
  