.adminDashboard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: 850px) {
    flex-direction: column;
    align-items: center;
  }

  .adminDashboardLinks {
    width: 20%;
    margin-right: 2rem;

    @media (max-width: 850px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }

  .adminDashboardContent {
    width: 75%;
    margin-top: 100px;
    margin-bottom: 100px;

    @media (max-width: 850px) {
      width: 90%;
    }
  }

  .card {
    background-color: #fff;
    padding: 2rem;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    border-radius: 5px;
    margin-bottom: 100px;

    h4 {
      display: flex;
      align-items: center;
      font-size: 18px;

     @media screen and ( max-width:450px){
      flex-direction: column;
      text-align: center;
     }

      p {
        margin-left: 10px;
        font-size: 16px;
        font-weight: 500;
      }
    }
  }
}
