.forgotPassword {
  margin-top: 50px;
  margin-bottom: 90px;

  h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    text-align: center;
  }

  form {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
      rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
      rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    padding: 45px;
    border-radius: 20px;

    .form-group {
      display: flex;
      flex-direction: column;
      margin-bottom: 1.5rem;
      width: 100%;

      label {
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
      }

      input {
        padding: 0.8rem;
        border: none;
        border-radius: 0.5rem;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        width: 95%;

        &:focus {
          outline: none;
        }
      }
    }

    .passwordDiv{
      position: relative;
      
      .passwordToggleBtn {
        position: absolute;
        top: 73%;
        right: 10px;
        transform: translateY(-50%);
        border: none;
        background-color: transparent;
        cursor: pointer;
        color: #888;
        font-size: 14px;
    
        &:focus {
          outline: none;
        }
      }
    }

    button[type="submit"] {
      margin-top: 20px;
      height: 50px;
      width: 100%;
      text-transform: uppercase;
      padding: 10px 20px;
      border-radius: 5px;
      border: none;
      background-color: #007bff;
      color: #fff;
      font-size: 1.2rem;
      cursor: pointer;
      transition: all 0.3s ease;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
        rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
        rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
      &:hover {
        background-color: #0062cc;
      }
    }
    .forgotPassword {
      text-transform: capitalize;
      cursor: pointer;
      color: #0062cc;
      margin-bottom: 0.5em;
      margin-top: 0.5em;
    }
    .formtext {
      display: flex;
      align-items: center;
      margin: 22px 10px;
      font-size: 20px;

      p {
        margin-right: 10px;
      }
      a {
        text-decoration: none;
      }
    }

    .passwordError{
      margin-top: -20px;
      font-size: 10px;
      color: red;
    }
  }


  @media only screen and (min-width: 768px) {
    form {
      width: 50%;
    }
  }
  @media only screen and (min-width: 1024px) {
    form {
      width: 30%;
    }
  }
}
