.userProfile {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: 850px) {
    flex-direction: column;
    align-items: center;
  }

  .userProfileLinks {
    width: 20%;
    margin-right: 2rem;

    @media (max-width: 850px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }

  .userProfileContent {
    width: 75%;
    margin-top: 5px;
    margin-left: 100px;

    @media (max-width: 850px) {
      width: 80%;
      margin-left: 0;
    }
    .userProfileFormContainer {
      max-width: 400px;
      width: 100%;

      form {
        display: flex;
        flex-direction: column;
        margin-bottom: 80px;

        .userProfileTitle {
          font-size: 20px;
          margin-bottom: 50px;
        }

        .userProfileDiv {
          margin-bottom: 15px;

          input,
          textarea {
            width: 100%;
            padding: 10px;
            font-size: 16px;
            border: 1px solid #ccc;
            border-radius: 5px;
          }
        }

        .Update-btn {
          padding: 10px 20px;
          font-size: 16px;
          background-color: #007bff;
          color: #fff;
          border: none;
          border-radius: 5px;
          cursor: pointer;
          width: 100%;

          &:hover {
            background-color: #0056b3;
          }
        }

        .userProfilePasswordDiv {
          .userProfilePasswordDivChild {
            position: relative;
            .passwordToggleBtn {
              position: absolute;
              top: 50%;
              right: -10px;
              transform: translateY(-50%);
              border: none;
              background-color: transparent;
              cursor: pointer;
              color: #888;
              font-size: 14px;

              &:focus {
                outline: none;
              }
            }
          }

          .passwordError {
            margin-top: 0px;
            font-size: 10px;
            color: red;
          }
        }
      }
    }
  }

  .card {
    background-color: #fff;
    padding: 2rem;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    margin-top: 50px;

    h4 {
      display: flex;
      align-items: center;
      font-size: 18px;

      @media screen and (max-width: 450px) {
        flex-direction: column;
        text-align: center;
      }

      p {
        margin-left: 10px;
        font-size: 16px;
        font-weight: 500;
      }
    }
  }
}
