.cartPageBoss {
  margin: 0 auto;
  max-width: 1285px;
  padding: 20px;
  background-color: #fff;
}

.cartPageContainer {
  display: flex;
  flex-direction: column;
}

.cartPageRow {
  margin-bottom: 20px;
  // flex: 3;
}

.cartPageContainerCol {
  width: 100%;
}

.cartPageContainerText-CheckLogin {
  margin-bottom: 10px;
}

.textCheckLogin {
  font-size: 16px;
  font-weight: bold;
}

.cartPageOtherContainer {
  margin-top: 20px;
  // flex: 1;
}

.cartPageOtherCard {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border: 1px solid #ccc;
  margin-bottom: 10px;
}

.cartPageImg {
  width: 60%;
  height: 60%;
  max-height: 600px;
  max-width: 600px;
  padding: 20px;
}

.cartPageOtherText {
  width: 150%;
  padding: 20px;

  @media screen and (max-width:950px) {
    width: 90%;
  }
}

.description {
  font-size: 14px;
  line-height: 1.5;
}

.prices {
  display: flex;
  align-items: center;
  margin-top: -20px;
  margin-bottom: -20px;
}

.card-price {
  font-size: 18px;
  font-weight: bold;
}

.card-quantity {
  font-size: 16px;
}

.card-quantity-price {
  font-size: 14px;
  span {
    color: red;
  }
}

.card-oldprice {
  text-decoration: line-through;
  margin: 0 15px;
}

.card-discount {
  background-color: #310d0d;
  color: #fff;
  padding: 2px 5px;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
}
.Rating {
  font-weight: bold;

  span {
    background-color: #310d0d;
    color: #fff;
    padding: 2px 5px;
    border-radius: 5px;
  }
}
.freeText {
  font-size: 14px;
  color: #aaa;
}

.cartPageOther-quantity {
  display: flex;
}

.quantityText {
  margin: 0 12px;
}

.cartPageOther-remove-btn {
  // margin-top: 10px;
  padding: 10px;
}

.cartPageOtherBtn {
  background-color: #aaa;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.cartPageOtherCart-summary {
  margin-top: 20px;
  padding: 10px;
  background-color: #f9f9f9;
}

.h2 {
  font-size: 20px;
  font-weight: bold;
}

.p {
  font-size: 16px;
}

.cartPageOtherAddress-Btn {
  margin-top: 10px;
}

.cartPageOther-btn-updateAddress {
  background-color: #aaa;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.cartPageOther-check-updateAddress {
  display: flex;
  justify-content: space-between;
}

.cartPageOther-pleaseLogin {
  background-color: #aaa;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.cartPageOther-Auth {
  margin-top: 10px;
}

.cartPageOther-payment-btn {
  background-color: #292929;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.loading {
  color: #ccc;
}

@media (max-width: 950px) {
  .cartPageOtherCard {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 50px;
    margin-top: 50px;
  }
}

@media (max-width: 768px) {
  .cartPageBoss {
    max-width: 450px;
  }
  .prices {
    flex-direction: column;
    align-items: flex-start;
  }
  .card-oldprice {
    margin: 0 5px;
  }
}
