.createCategories {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: 850px) {
    flex-direction: column;
    align-items: center;
  }

  .createCategoriesLinks {
    width: 20%;
    margin-right: 2rem;

    @media (max-width: 850px) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2rem;
    }
  }


  .createCategoriesContent {
    width: 75%;
    margin-top: 100px;
    margin-bottom: 100px;

    @media (max-width: 850px) {
      width: 90%;
    }
    .card {
      background-color: #fff;
      padding: 2rem;
      box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
      border-radius: 5px;
      margin-bottom: 100px;

      h4 {
        font-size: 24px;
        margin-bottom: 20px;
      }
      .editForm {
        margin-bottom: 20px;
      }
      .tableSection {
        table {
          width: 100%;
          border-collapse: collapse;
          th,
          td {
            padding: 12px;
            text-align: left;
            border-bottom: 1px solid #3b3333;
          }
          th {
            background-color: #7c6969;
          }
        }
        .editBtn {
          margin-right: 10px;
          padding: 6px 12px;
          border: 1px solid #ccc;
          border-radius: 4px;
          background-color: rgb(31, 31, 255);
          color: #ccc;
          cursor: pointer;
          transition: all .25s ease;
          &:hover {
            background-color: rgb(31, 31, 255,.8);
          }
        }
        .delBtn {
          padding: 6px 12px;
          border: 1px solid #ccc;
          border-radius: 4px;
          background-color: rgb(238, 11, 11);
          color: #ccc;
          cursor: pointer;
          transition: all .25s ease;
          &:hover {
            background-color: rgb(238, 11, 11,.8);
          }
        }
      }
    }
  }

  @media only screen and (max-width: 850px) {
    .createCategoriesContent {
      .card {
        h4 {
          font-size: 20px;
        }
      }
    }
  }

  @media only screen and (max-width: 450px) {
    .createCategoriesContent {
      .card {
        h4 {
          font-size: 18px;
        }
        .editForm {
          margin-bottom: 10px;
        }
        .tableSection {
          table {
            th,
            td {
              padding: 8px;
            }
          }
          .editBtn,
          .delBtn {
            padding: 4px 8px;
            font-size: 12px;
          }
        }
      }
    }
  }
}


.modalContainer {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;

  #modal {
    background-color: #fff;
    padding: 40px 100px;
    padding-top: 55px;
    border-radius: 5px;
    max-width: 600px;
    position: relative;
    z-index: 2;
  }

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &:hover {
      cursor: pointer;
    }
  }

  .cross {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    font-size: 24px;
    padding: 10px;
  }
}
