.CategoryProductBoss {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 100px;
}

.CategoryProductContainer {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.CategoryProduct-text-name {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.CategoryProduct-text-result-name {
  font-size: 16px;
  color: #999;
}

.CategoryProductContent {
  width: 100%;
  margin: 0 10px;
}

.CategoryProduct-flex {
  display: flex;
  flex-wrap: wrap;
  // justify-content: center;
  margin: 0 10px;
}

.CategoryProduct-card {
  width: 25%;
  margin: 0 2.5%;
  padding: 10px;
  border: 1px solid #ccc;
  transition: 0.2s;
  cursor: pointer;
}

.CategoryProduct-card:hover {
  box-shadow: 0 0 5px 0 #ccc;
}

.CategoryProduct-card img {
  width: 100%;
}

.CategoryProduct-card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.CategoryProduct-card-name-price {
  display: flex;
  flex-direction: column;
}

.CategoryProduct-card-title {
  font-size: 16px;
  font-weight: bold;
}

.CategoryProduct-card-description {
  font-size: 14px;
  color: #999;
  max-height: 70px;
  overflow: hidden;
  margin-top: -10px;
  margin-bottom: -10px;
}

.CategoryProduct-priceContainer {
  display: flex;
  align-items: center;
}

.CategoryProduct-card-price {
  font-size: 16px;
  font-weight: bold;

  span{
    color: red;
  }
}

.CategoryProduct-card-oldPrice {
  font-size: 12px;
  color: #999;
  text-decoration: line-through;
  padding: 0 2px;
}

.CategoryProduct-card-discount {
  font-weight: bold;
  font-size: 14px;
  color: #ffffff;
  background-color: #35050c;
  padding: 5px;
  border-radius: 5px;
}

.CategoryProduct-Rating {
  font-weight: bold;
  span {
    background-color: #310d0d;
    color: #fff;
    border-radius: 5px;
    padding: 2px 5px;
  }
}

.CategoryProduct-Delivery {
  font-size: 14px;
  color: #999;
  margin-top: -10px;
}

.CategoryProduct-card-buttons {
  display: flex;
  justify-content: space-between;
}

.CategoryProduct-MoreDetails-btn {
  background-color: #8f4e4e;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.CategoryProduct-MoreDetails-btn:hover {
  background-color: #aaa;
}

.btnAddToCart {
  background-color: #377cfc;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.btnAddToCart:hover {
  background-color: rgb(55, 124, 252, 0.6);
}

.btnLoading {
  background-color: #ccc;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  display: none;
}

.loadingDiv {
  text-align: center;
}

@media (max-width: 1100px) {
  .CategoryProduct-card {
    width: 50%;
  }
}

@media (max-width: 550px) {
  .CategoryProductRow {
    flex-direction: column;
  }

  .CategoryProduct-card {
    width: 100%;
    margin: 0 0 10px;
    .CategoryProduct-card-buttons {
      .CategoryProduct-MoreDetails-btn,
      btnAddToCart {
        padding: 5px 10px;
      }
    }
  }
}
