.home-slider {
  position: relative;
  width: 100%;
  margin-bottom: 10px;

  .slider-image {
    height: 300px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .slider-navigation {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    display: flex;
    justify-content: space-between;

    .left-icon,
    .right-icon {
      background: none;
      border: none;
      cursor: pointer;
      font-size: 24px;
      color: #ffffff;
      transition: color 0.3s ease;

      &:hover {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }

  @media (max-width: 950px) {
    .slider-image {
      height: 250px;
    }
  }

  @media (max-width: 768px) {
    .slider-image {
      height: 200px;
    }

    .slider-navigation {
      .left-icon,
      .right-icon {
        font-size: 20px;
      }
    }
  }

  @media (max-width: 450px) {
    .slider-image {
      height: 150px;
    }

    .slider-navigation {
      .left-icon,
      .right-icon {
        font-size: 16px;
      }
    }
  }
}
