.ProductDetails {
  .ProductDetailsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding: 50px;
    max-width: 1285px;
    margin: 0 auto;

    .imgContainer {
      img {
        width: 100%;
        height: 60%;
        max-height: 450px;
      }
    }

    .product-details-info {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      width: 50%;

      h1 {
        font-size: 2rem;
        font-weight: bold;
        margin: 0;
      }

      hr {
        border: none;
        height: 1px;
        background-color: #ddd;
        margin: 1rem 0;
      }

      .product-details-content {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        h3 {
          font-size: 1.5rem;
          margin: 0;
        }

        .description {
          margin: 0;

          span {
            font-weight: bold;
          }
        }

        .prices {
          display: flex;
          gap: 0.25rem;
          margin-top: 5px;
          margin-bottom: 5px;

          .card-price {
            font-size: 1.25rem;
            font-weight: bold;
            margin: 0;
            span {
              color: rgb(238, 46, 46);
            }
          }

          .card-oldprice {
            font-size: 1rem;
            text-decoration: line-through;
            margin: 0 10px;
          }

          .card-discount {
            font-size: 1rem;
            margin: 0;
            background-color: #310d0d;
            color: #fff;
            padding: 2px 5px;
          }
        }

        .card-text {
          margin: 0;

          span {
            display: inline-block;
            padding: 8px;
            margin-right: 8px;
            border: 1px solid #ccc;
            // cursor: pointer;
          }
        }
        .card-text-Rating {
          font-weight: bold;
          span {
            background-color: #310d0d;
            color: #fff;
            padding: 2px 5px;
          }
        }
        .freeText {
          font-weight: bold;
          margin: 0;
        }

        .categoryName {
          margin: 0;
        }
      }

      .addtocartBtn {
        padding: 0.75rem 2rem;
        font-size: 1rem;
        font-weight: bold;
        color: white;
        background-color: #0066cc;
        border: none;
        border-radius: 0.25rem;
        cursor: pointer;
        transition: background-color 0.2s ease-in-out;

        &:hover {
          background-color: #004499;
        }
      }
    }
  }

  hr {
    border: none;
    height: 1px;
    background-color: #ddd;
    margin: 1.5rem 0;
  }

  .similar-products {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 20px;

    h4 {
      font-size: 1.5rem;
      font-weight: bold;
      margin: 0;
    }

    .nosimilerProductsFound {
      font-size: 1.25rem;
      font-weight: bold;
      margin: 0;
    }

    .similerProductsFoundContainer {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      grid-gap: 1rem;

      .card {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        cursor: pointer;
        transition: transform 0.2s ease-in-out;

        &:hover {
          transform: translateY(-5px);
        }

        img {
          width: 100%;
          max-height: 400px;
        }

        .card-body {
          width: 100%;
          display: flex;
          flex-direction: column;
          gap: 0.25rem;

          .card-name-price {
            display: flex;
            flex-direction: column;
            gap: 0.25rem;

            .card-title {
              font-size: 1rem;
              font-weight: bold;
              margin: 0;
              margin-bottom: 8px;
            }

            .prices {
              display: flex;
              align-items: center;
              margin: 5px 0;

              .card-price {
                font-size: 14px;
                font-weight: bold;
                margin: 0;

                span {
                  color: rgb(238, 46, 46);
                }
              }

              .card-oldprice {
                font-size: 12px;
                text-decoration: line-through;
                // margin: 0 8px;
                // padding: 0 3px;
              }

              .card-discount {
                font-size: 14px;
                margin: 0;
                background-color: #310d0d;
                color: #fff;
                padding: 2px 5px;
                border-radius: 5px;
              }
            }

            .card-text-Rating {
              font-weight: bold;
              span {
                background-color: #310d0d;
                color: #fff;
                padding: 2px 5px;
                border-radius: 5px;
              }
            }
          }

          .freeText {
            font-weight: bold;
            margin: 0;
          }
        }
      }
    }
  }

  @media (max-width: 950px) {
    .ProductDetailsContainer {
      flex-direction: column;

      .product-details-info {
        width: 80%;
        .product-details-content {
          .description {
            font-size: 0.875rem;
          }
        }
      }
    }
  }

  @media (max-width: 768px) {
    .ProductDetailsContainer {
      .product-details-info {
        width: 90%;

        h1 {
          font-size: 1.5rem;
        }
        .product-details-content {
          h3 {
            font-size: 1.2rem;
          }
          .prices {
            align-items: center;
          }
          .description {
            font-size: 0.875rem;
          }
        }
      }
    }

    .similar-products {
      .similerProductsFoundContainer {
        .card {
          .card-body {
            .card-name-price {
              .card-title {
                font-size: 0.875rem;
              }
            }

            .freeText {
              font-size: 0.875rem;
            }
          }
        }
      }
    }
  }
}
