.orders {
  margin: 0 auto;
  max-width: 1285px;
  padding: 20px;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (max-width: 576px) {
    width: 100%;
    padding: 0 10px;
  }
}

.userDashboardLinks {
  margin-bottom: 20px;
}

.userDashboardContent {
  padding: 20px;
}

.card {
  border: 1px solid #ccc;
  margin-bottom: 20px;
  padding: 20px;
@media (max-width: 500px) {
  padding: 6px;
 }
}

.orderHeading {
  margin-top: 0;
}

.orderBoss {
  margin-bottom: 20px;
}

.table {
  width: 100%;
@media (max-width: 500px) {
  font-size: 10px;
 }
}

.table thead th {
  background-color: #eee;
  padding: 10px;
@media (max-width: 500px) {
  padding: 3px;
 }
}

.table td {
  padding: 10px;
@media (max-width: 500px) {
  padding: 3px;
 }
}

.ordercontentBoss {
  display: flex;
  flex-wrap: wrap;
}

.ordercontent {
  width: 70%;
  margin: 10px;
}

.ordercontentImg {
  width: 100%;
}

.cardimg {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.ordercontentText {
  padding: 10px;
}

.ordercontentName {
  font-weight: bold;
}

.ordercontentDescription {
  color: #777;
}

.ordercontentPrice {
  color: red;
}