.terms-and-conditions {
    .container {
      max-width: 1000px;
      margin: 0 auto;
      padding: 40px;
  
      @media (max-width: 768px) {
        padding: 30px;
      }
  
      @media (max-width: 480px) {
        padding: 20px;
      }
  
      h2 {
        font-size: 36px;
        font-weight: bold;
        margin-bottom: 20px;
      }
  
      h3 {
        font-size: 24px;
        font-weight: bold;
        margin-top: 40px;
        margin-bottom: 20px;
      }
  
      p {
        font-size: 18px;
        line-height: 1.5;
        margin-bottom: 20px;
  
        span {
          display: inline-block;
          width: 40px;
        }
      }
  
      a {
        color: #007bff;
        text-decoration: underline;
  
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
  